import React, {useEffect} from "react";
import style from "./Login.module.sass";
import {connect} from "react-redux";
import {login, resetError} from "../../redux/authReducer";
import {Redirect} from "react-router-dom";
import {Field, Form, Formik} from "formik";
import * as Yup from "yup";
import {CustomCheckbox, CustomTextInput} from "../../common/FormsControls/FormsControlsFormik";
import cn from "classnames";


function validateCaptcha(value) {
   let error;
   if (!value) {
      error = 'Required';
   }
   return error;
}

const LoginForm = ({captchaUrl, onSubmit, errorMessage, ...props}) => (

   <div className={style.login}>
      <Formik
         initialValues={{
            email: '',
            password: '',
            rememberMe: false,
            captcha: ''
         }}
         onSubmit={(values, {setSubmitting, resetForm}) => {
            onSubmit(values)
            // setTimeout(() => {
            // alert(JSON.stringify(values, null, 2));
            // onSubmit(values)
            // resetForm();
            setSubmitting(false);
            // }, 1000)
         }}
         validationSchema={Yup.object().shape({
            email: Yup.string()
               .email('Invalid email address')
               .required('Required'),
            password: Yup.string()
               .required('Required')
               .min(3, 'Must be at least 3 characters'),
            // rememberMe: Yup.boolean()
            //    .oneOf([true], 'You must Checked')
         })}
      >
         {(props) => {

            const {
               values,
               touched,
               errors,
               isSubmitting,
               handleChange,
               dirty,
               handleBlur,
               handleReset
            } = props
            // console.log(props)
            return (
               <Form>
                  <div>
                     <Field
                        component="input"
                        name="email"
                        placeholder="email"
                        value={values.email}
                        onChange={handleChange}
                        className={cn(style.textInput,
                           {[style.error]: (errors.email && touched.email)}
                        )}
                     />
                  </div>

                  {errors.email && touched.email && (
                     <div className={style.inputFeedback}>{errors.email}</div>
                  )}

                  <div>
                     <Field
                        component="input"
                        type="password"
                        name="password"
                        placeholder="password"
                        value={values.password}
                        onChange={handleChange}
                        className={cn(style.textInput,
                           {[style.error]: (errors.password && touched.password)}
                        )}
                     />
                  </div>

                  {errors.password && touched.password && (
                     <div className={style.inputFeedback}>{errors.password}</div>
                  )}

                  <div className={style.checkboxItem}>
                     <CustomCheckbox name="rememberMe">
                        Remember Me
                     </CustomCheckbox>
                  </div>

                  {captchaUrl && <img src={captchaUrl} alt=""/>}

                  {captchaUrl && <div>
                     <Field
                        validate={validateCaptcha}
                        component="input"
                        name="captcha"
                        placeholder="Captcha"
                        value={values.captcha}
                        onChange={handleChange}
                        className={cn(style.textInput,
                           {[style.error]: (errors.captcha && touched.captcha)}
                        )}
                     />
                  </div>}

                  {errors.captcha && touched.captcha && (
                     <div className={style.inputFeedback}>{errors.captcha}</div>
                  )}

                  {
                     errorMessage && <div className={style.formCommonError}>
                        <span>{errorMessage}</span>
                     </div>
                  }

                  <div className={style.buttonItem}>
                     <button type="submit"
                             disabled={isSubmitting}
                     >
                        {isSubmitting ? 'Loading...' : 'Log In'}
                     </button>
                  </div>
               </Form>
            )
         }}
      </Formik>
   </div>
)
const Login = ({login, isAuth, captchaUrl, errorMessage, resetError, ...props}) => {

   useEffect(() => {
      resetError();
   },[isAuth])

   const onSubmit = (values) => {
      login(
         values.email,
         values.password,
         values.rememberMe,
         values.captcha
      )
   }

   if (isAuth) {
      return <Redirect to={"/profile"}/>
   }
   return <div className={style.login}>
      <LoginForm onSubmit={onSubmit} captchaUrl={captchaUrl} errorMessage={errorMessage}/>
   </div>
}

const mapStateToProps = (state) => ({
   isAuth: state.authReducer.isAuth,
   captchaUrl: state.authReducer.captchaUrl,
   errorMessage: state.authReducer.errorMessage,
})


export default connect(mapStateToProps, {login, resetError})(Login);